<template>
 <div class="div-warp">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>编辑售货机</span>
        </div>
        <el-form :model="addForm" :rules="Rules" class="formWidth"  ref="addForm" @submit.prevent="onSubmit">
          <el-form-item label="售货机编号" prop="siteCode" size="small" :label-width="formLabelWidth">
            <el-input v-model="addForm.siteCode" ></el-input>
          </el-form-item>
          <el-form-item label="售货机名称" prop="siteName" size="small" :label-width="formLabelWidth">
            <el-input v-model="addForm.siteName" auto-complete="off"  ></el-input>
          </el-form-item>
          <el-form-item label="厂家" prop="factoryId" size="small" :label-width="formLabelWidth">
            <el-select v-model="addForm.factoryId" size="small" placeholder="请选择厂家">
                <el-option
                  v-for="item in ManufactorOptions"
                  :key="item.dictCode"
                  :label="item.dictLabel"
                  :value="item.dictCode">
                </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="货柜类型" prop="cabinetType" size="small" :label-width="formLabelWidth">
            <el-select v-model="addForm.cabinetType" size="small" placeholder="请选择负责人姓名">
               <el-option
                  v-for="item in CabinetOptions"
                  :key="item.dictCode"
                  :label="item.dictLabel"
                  :value="item.dictCode">
                </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="机型" prop="deviceId" size="small" :label-width="formLabelWidth">
            <el-select v-model="addForm.deviceId" size="small" placeholder="请选择负责人姓名">
              <el-option
                  v-for="item in modelOptions"
                  :key="item.deviceId"
                  :label="item.deviceId"
                  :value="item.deviceId">
              </el-option>
            </el-select>
          </el-form-item>
          

          <el-form-item label="点位"  prop="pointId" size="small"  :label-width="formLabelWidth">
            <el-autocomplete
              v-model="addForm.name"
              :fetch-suggestions="querySearchAsync"
              value-key="name"
              placeholder="输入搜索点位"
              @select="handleSelect"

            ></el-autocomplete>
          </el-form-item>



          <el-form-item label="网络类型" prop="netWork" size="small" :label-width="formLabelWidth">
            <el-select v-model="addForm.netWork" size="small" placeholder="请选择负责人姓名">
              <el-option
                  v-for="item in networkOptions"
                  :key="item.dictValue"
                  :label="item.dictLabel"
                  :value="item.dictValue">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="售卖状态" prop="sellState" size="small" :label-width="formLabelWidth">
            <el-select v-model="addForm.sellState" size="small" placeholder="请选择负责人姓名">
              <el-option
                  v-for="item in stateOptions"
                  :key="item.dictValue"
                  :label="item.dictLabel"
                  :value="item.dictValue">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="支付方式" size="small" :label-width="formLabelWidth">

             <el-checkbox-group v-model="palychek" @change="handleCheckedCitiesChange">
              <el-checkbox label="1">支付宝</el-checkbox>
              <el-checkbox label="2">微信</el-checkbox>
              <el-checkbox label="3">现金</el-checkbox>

            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="视频播放"  size="small" :label-width="formLabelWidth">
            <el-radio-group v-model="addForm.mediaType" size="mini" >
              <el-radio label="1" border>支持</el-radio>
              <el-radio label="2" border>不支持</el-radio>
            </el-radio-group>
          </el-form-item>

         
        </el-form>

      <div class="bntBox">
          <el-button @click="dialogAddTableVisible = false">取 消</el-button>
          <el-button type="primary" @click="addSubmitForm('addForm')">提交</el-button>
      </div>
      </el-card>
  </div>
</template>       

<script>
import { addMachine,getMachineSelect,getVendingModel,getPositionList } from '@/api/equipment'
import NProgress from 'nprogress'
export default {
  data() {
        return {

          // 厂家、货柜类型、网络类型、售卖状态查询接口

          ManufactorOptions:[],//厂家列表---下拉
          CabinetOptions:[],//货柜类型---下拉
          stateOptions:[],//售卖状态--下拉
          networkOptions:[],//网络--下拉
          modelOptions:[],//机型希腊
          options:[],
          palychek:[],
          restaurants:[],//点位
          companyName: '',
          value:"",
          input:'',
          formLabelWidth: '120px',
          addForm: {
              siteCode:"",  //售货机编码
              siteName:"",  //售货机名称
              factoryId:"", // 厂家编号
              cabinetType:"",  //货柜类型 01:商店机 02:弹簧机 03:格子机
              deviceId:"",  //机型编码
              districtId:"", //区域编号
              lineId:"", //线路编号
              pointId:"", //点位编号:'',
              pointName:'',//点位名称
              payType:'', //支付类型 可多选从左到右每一位代表支持一种。(1支付宝 2:微信 3:百度钱包 )
              mediaType:"1", //播放视频 1:支持 2:无
              netWork:"", //网络制式 1:GPRS  2:网口
              sellState:"", //售卖状态 1:可售卖 2:不可售卖
              description:""  //描述
          },
          Rules:{
              siteCode:[
                { required: true, message: ' ', trigger: 'change' }
              ],
              siteName:[
                { required: true, message: ' ', trigger: 'change' }
              ],
              dictLabel:[
                { required: true, message: ' ', trigger: 'change' },
               
              ],
              factoryId:[
                { required: true, message: ' ', trigger: 'change' },
              ],
              cabinetType:[
                 { required: true, message: ' ', trigger: 'change' },
              ],
              netWork:[
                 { required: true, message: ' ', trigger: 'change' },
              ],
              sellState:[
                 { required: true, message: ' ', trigger: 'change' },
              ],
              deviceId:[
                 { required: true, message: ' ', trigger: 'change' },
              ],
              pointId:[
                { required: true, message: ' ', trigger: 'change' },
              ]
          }
         
        }
  },
  watch: {
   
  },
  methods: {
    handleCheckedCitiesChange(value) {
        this.addForm.payType = value.join(',')
    },
    addSubmitForm(formName) {//提交新增窗口
        this.$refs[formName].validate((valid) => {
          if (valid) {
          this.$confirm('确认提交吗？', '提示', {}).then(() => {
               NProgress.start();
                      let para = Object.assign({}, this.addForm);
                      addMachine(para).then((res) => {
                            if(res.zhead.reTCode=="0000"){
                            NProgress.done();
                            this.$notify({
                              title: '成功',
                              message: res.zhead.retMsg,
                              type: 'success'
                            });
                            this.$refs['addForm'].resetFields();
                            this.$router.push(
                                  {path:'/equipment/machine/index'}
                            );
                            }else{
                              this.$notify({
                                title: '失败',
                                message: res.zhead.retMsg,
                                type: 'error'
                              });
                          }
                      });
          });
            //this.dialogAddTableVisible = false
          } else {
            console.log('error submit!!');
            return false;
          }
        });
    },
   
    resetForm(formName) {//重置表单
        this.$refs[formName].resetFields();
    },

    getMachineSelectList(type) {
      var _this = this;
      const listQuery = {
        dictType:type,
      }
      getMachineSelect(listQuery).then(response => {
         if(response.zhead.reTCode==="0000"){
         
          NProgress.done();
          /*
          字典类型,厂商：sys_factory；货柜类型sys_cabinet_type；网络类型：sys_net_work；售卖状态：sys_sell_state
          */ 

          if(type==="sys_factory")
          {
            _this.ManufactorOptions= response.zbody.datas.rows;//厂家列表---下拉
             console.log("厂家列表：：",_this.ManufactorOptions)
          }
          else if(type==="sys_cabinet_type")
          {
             _this.CabinetOptions= response.zbody.datas.rows;//货柜类型---下拉
             console.log("货柜类型：：",_this.CabinetOptions)
          }
          else if(type==="sys_net_work")
          {
              _this.networkOptions= response.zbody.datas.rows;//网络类型---下拉
              console.log("网络类型：：",_this.networkOptions)
          }
          else if(type==="sys_sell_state")
          {
             _this.stateOptions= response.zbody.datas.rows;//售卖状态--下拉
             console.log("售卖状态：：",_this.stateOptions)
          }
         }else{
             this.$notify({
              title: '失败',
              message: res.zhead.retMsg,
              type: 'error'
            });
         }
      })
    },
    getModel(){
      var _this = this;
      const listQuery = {
      }
      getVendingModel(listQuery).then(response => {
           if(response.zhead.reTCode==="0000"){
            _this.modelOptions=response.zbody.datas.rows
            console.log("机型：：",response.zbody.datas.rows)
            NProgress.done();
           }else{
               this.$notify({
                title: '失败',
                message: res.zhead.retMsg,
                type: 'error'
              });
           }
        })
    },
    querySearchAsync(queryString, cb) {//输入搜索点位
        var restaurants = this.restaurants;
        var results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;
        var _this = this;
        const listQuery = {
          name:_this.companyName
        }
        getPositionList(listQuery).then(response => {
          
          _this.restaurants = response.zbody.datas.rows
          console.log(_this.restaurants)
          cb(results);
        })
    },
    createStateFilter(queryString) {
        return (state) => {
          return (state.name.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
    },
    handleSelect(item) {
        this.addForm.pointId = item.pointId
        this.addForm.pointName = item.name
        console.log(item.pointId);
    }
  },

  mounted () {
        this.getMachineSelectList("sys_factory")
        this.getMachineSelectList("sys_cabinet_type")
        this.getMachineSelectList("sys_net_work")
        this.getMachineSelectList("sys_sell_state")
        this.getModel()
  },
  components: {
  }
}
</script>
