import request from '@/utils/request'
import axios from 'axios'
let base = process.env.API_HOST///manage


/*
		售后机管理
*/ 

export const getRevenue = params =>{
	  return axios.post(base + `/index/selectMSaleSummry`, params).then(res => res.data)
}

//新增售货机
export const addMachine = params =>{
	  return axios.post(base + `/vending/add`, params).then(res => res.data)
}

//厂家、货柜类型、网络类型、售卖状态查询接口
/*
字典类型,厂商：sys_factory；货柜类型sys_cabinet_type；网络类型：sys_net_work；售卖状态：sys_sell_state
*/ 

export function getMachineSelect(params) {
  return request({
    url: base+'/dict/data/list',
    method: 'get',
    params
  })
}
//查询机型
export function getVendingModel(params) {
  return request({
    url: base+'/vendingModel/list',
    method: 'get',
    params
  })
}
//查询点位
export function getPositionList(params) {
  return request({
    url: base+'/vendingPoint/list',
    method: 'get',
    params
  })
}
//售卖机查询
export function getSelectNVendingList(params) {
  return request({
    url: base+'/vending/selectNVendingList',
    method: 'get',
    params
  })
}

//获取一个机型查询接口

export function vendingModelList(params) {
  return request({
    url: base+'/vendingModel/get',
    method: 'get',
    params
  })
}
//查商品
export function getCommodity(params) {
  return request({
    url: base+'/productInfo/list',
    method: 'get',
    params
  })
}
